import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";

export interface RestaurantTypeSet {
  zh: string;
  en: string;
  pt: string;
}

export const useAllRestaurantTypes = () => {
  return useQuery({
    queryKey: ["all-restaurant-types"],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("restaurants")
        .select("type, type_en, type_pt");

      if (error) throw error;

      // Build a map of unique type sets
      const typeMap = new Map<string, RestaurantTypeSet>();

      data.forEach((r) => {
        const types = r.type || [];
        const typesEn = r.type_en || [];
        const typesPt = r.type_pt || [];

        // Align types by index
        const maxLen = Math.max(types.length, typesEn.length, typesPt.length);
        for (let i = 0; i < maxLen; i++) {
          const zh = types[i] || "";
          const en = typesEn[i] || "";
          const pt = typesPt[i] || "";
          
          // Use zh as primary key, but also check en/pt for dedup
          const key = `${zh}|${en}|${pt}`;
          if (zh || en || pt) {
            if (!typeMap.has(key)) {
              typeMap.set(key, { zh, en, pt });
            }
          }
        }
      });

      // Also extract individual unique values for each language
      const uniqueZh = new Set<string>();
      const uniqueEn = new Set<string>();
      const uniquePt = new Set<string>();

      typeMap.forEach((t) => {
        if (t.zh) uniqueZh.add(t.zh);
        if (t.en) uniqueEn.add(t.en);
        if (t.pt) uniquePt.add(t.pt);
      });

      return {
        typeSets: Array.from(typeMap.values()),
        uniqueZh: Array.from(uniqueZh).sort(),
        uniqueEn: Array.from(uniqueEn).sort(),
        uniquePt: Array.from(uniquePt).sort(),
      };
    },
  });
};
