import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { useAuth } from "@/contexts/AuthContext";
import { toast } from "sonner";
import i18n from "i18next";

export interface DishImage {
  id: string;
  restaurant_id: string;
  image_url: string;
  sort_order: number;
  created_at: string;
  created_by: string | null;
}

export const useDishImages = (restaurantId: string) => {
  return useQuery({
    queryKey: ["dish-images", restaurantId],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("restaurant_dish_images")
        .select("*")
        .eq("restaurant_id", restaurantId)
        .order("sort_order", { ascending: true });

      if (error) throw error;
      return data as DishImage[];
    },
    enabled: !!restaurantId,
  });
};

export const useAddDishImage = () => {
  const { user } = useAuth();
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async ({ restaurantId, file }: { restaurantId: string; file: File }) => {
      if (!user) throw new Error("Must be logged in");

      // Check current image count
      const { data: existingImages, error: countError } = await supabase
        .from("restaurant_dish_images")
        .select("id")
        .eq("restaurant_id", restaurantId);

      if (countError) throw countError;
      if (existingImages && existingImages.length >= 10) {
        throw new Error("MAX_IMAGES_REACHED");
      }

      // Upload image
      const fileExt = file.name.split(".").pop();
      const fileName = `${restaurantId}/${Date.now()}-${Math.random().toString(36).substring(7)}.${fileExt}`;

      const { error: uploadError } = await supabase.storage
        .from("dish-images")
        .upload(fileName, file);

      if (uploadError) throw uploadError;

      // Get public URL
      const { data: { publicUrl } } = supabase.storage
        .from("dish-images")
        .getPublicUrl(fileName);

      // Get next sort order
      const nextSortOrder = existingImages ? existingImages.length : 0;

      // Insert record
      const { data, error: insertError } = await supabase
        .from("restaurant_dish_images")
        .insert({
          restaurant_id: restaurantId,
          image_url: publicUrl,
          sort_order: nextSortOrder,
          created_by: user.id,
        })
        .select()
        .single();

      if (insertError) throw insertError;
      return data;
    },
    onSuccess: (_, variables) => {
      queryClient.invalidateQueries({ queryKey: ["dish-images", variables.restaurantId] });
      toast.success(i18n.t("dishImages.uploadSuccess"));
    },
    onError: (error: any) => {
      if (error.message === "MAX_IMAGES_REACHED") {
        toast.error(i18n.t("dishImages.maxImagesReached"));
      } else {
        toast.error(i18n.t("dishImages.uploadFailed"));
      }
    },
  });
};

export const useDeleteDishImage = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async ({ imageId, restaurantId, imageUrl }: { imageId: string; restaurantId: string; imageUrl: string }) => {
      // Delete from storage
      const urlParts = imageUrl.split("/dish-images/");
      if (urlParts[1]) {
        await supabase.storage
          .from("dish-images")
          .remove([urlParts[1]]);
      }

      // Delete record
      const { error } = await supabase
        .from("restaurant_dish_images")
        .delete()
        .eq("id", imageId);

      if (error) throw error;
      return { restaurantId };
    },
    onSuccess: (data) => {
      queryClient.invalidateQueries({ queryKey: ["dish-images", data.restaurantId] });
      toast.success(i18n.t("dishImages.deleteSuccess"));
    },
    onError: () => {
      toast.error(i18n.t("dishImages.deleteFailed"));
    },
  });
};

export const useReorderDishImages = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async ({ images, restaurantId }: { images: { id: string; sort_order: number }[]; restaurantId: string }) => {
      const updates = images.map(img => 
        supabase
          .from("restaurant_dish_images")
          .update({ sort_order: img.sort_order })
          .eq("id", img.id)
      );

      await Promise.all(updates);
      return { restaurantId };
    },
    onSuccess: (data) => {
      queryClient.invalidateQueries({ queryKey: ["dish-images", data.restaurantId] });
    },
  });
};
