import { useState, useRef } from "react";
import { useTranslation } from "react-i18next";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Upload, X, Loader2, ImageIcon, GripVertical } from "lucide-react";
import { useDishImages, useAddDishImage, useDeleteDishImage, useReorderDishImages, DishImage } from "@/hooks/useDishImages";
import {
  AlertDialog,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  DragEndEvent,
} from "@dnd-kit/core";
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  useSortable,
  rectSortingStrategy,
} from "@dnd-kit/sortable";
import { CSS } from "@dnd-kit/utilities";

interface DishImageManagerProps {
  restaurantId: string;
}

interface SortableImageProps {
  image: DishImage;
  index: number;
  onDelete: (id: string, url: string) => void;
}

function SortableImage({ image, index, onDelete }: SortableImageProps) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: image.id });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    zIndex: isDragging ? 10 : 1,
    opacity: isDragging ? 0.8 : 1,
  };

  return (
    <div
      ref={setNodeRef}
      style={style}
      className={`relative group aspect-square rounded-lg overflow-hidden border ${
        isDragging ? "ring-2 ring-primary shadow-lg" : ""
      }`}
    >
      {/* Drag Handle */}
      <div
        {...attributes}
        {...listeners}
        className="absolute top-1 left-1 z-10 p-1 bg-black/60 rounded cursor-grab active:cursor-grabbing opacity-0 group-hover:opacity-100 transition-opacity"
      >
        <GripVertical className="w-4 h-4 text-white" />
      </div>

      <img
        src={image.image_url}
        alt={`Dish ${index + 1}`}
        className="w-full h-full object-cover"
        draggable={false}
      />
      
      <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
        <Button
          type="button"
          variant="destructive"
          size="icon"
          className="h-8 w-8"
          onClick={(e) => {
            e.preventDefault();
            e.stopPropagation();
            onDelete(image.id, image.image_url);
          }}
        >
          <X className="w-4 h-4" />
        </Button>
      </div>
      
      <div className="absolute bottom-1 right-1 bg-black/60 text-white text-xs px-1.5 py-0.5 rounded">
        {index + 1}
      </div>
    </div>
  );
}

export function DishImageManager({ restaurantId }: DishImageManagerProps) {
  const { t } = useTranslation();
  const fileInputRef = useRef<HTMLInputElement>(null);
  const { data: images = [], isLoading } = useDishImages(restaurantId);
  const addImage = useAddDishImage();
  const deleteImage = useDeleteDishImage();
  const reorderImages = useReorderDishImages();
  const [deleteConfirm, setDeleteConfirm] = useState<{ id: string; url: string } | null>(null);
  const [localImages, setLocalImages] = useState<DishImage[]>([]);
  const [isDeleting, setIsDeleting] = useState(false);

  // Sync local state with fetched images
  const displayImages = localImages.length > 0 ? localImages : images;

  const sensors = useSensors(
    useSensor(PointerSensor, {
      activationConstraint: {
        distance: 8,
      },
    }),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;

    if (over && active.id !== over.id) {
      const oldIndex = displayImages.findIndex((img) => img.id === active.id);
      const newIndex = displayImages.findIndex((img) => img.id === over.id);

      const newOrder = arrayMove(displayImages, oldIndex, newIndex);
      setLocalImages(newOrder);

      // Update sort orders in database
      const updates = newOrder.map((img, idx) => ({
        id: img.id,
        sort_order: idx,
      }));

      reorderImages.mutate(
        { images: updates, restaurantId },
        {
          onSuccess: () => {
            setLocalImages([]);
          },
          onError: () => {
            setLocalImages([]);
          },
        }
      );
    }
  };

  const handleFileSelect = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    if (file.size > 5 * 1024 * 1024) {
      return;
    }

    if (!file.type.startsWith("image/")) {
      return;
    }

    await addImage.mutateAsync({ restaurantId, file });
    
    if (fileInputRef.current) {
      fileInputRef.current.value = "";
    }
  };

  const handleDeleteClick = (id: string, url: string) => {
    setDeleteConfirm({ id, url });
  };

  const handleDeleteConfirm = async () => {
    if (!deleteConfirm || isDeleting) return;
    
    const { id, url } = deleteConfirm;
    setIsDeleting(true);
    
    try {
      await deleteImage.mutateAsync({
        imageId: id,
        restaurantId,
        imageUrl: url,
      });
      // Only close dialog after successful deletion
      setDeleteConfirm(null);
    } catch (error) {
      console.error("Delete failed:", error);
    } finally {
      setIsDeleting(false);
    }
  };

  const handleDialogClose = (open: boolean) => {
    // Only allow closing if not currently deleting
    if (!open && !isDeleting) {
      setDeleteConfirm(null);
    }
  };

  const canAddMore = displayImages.length < 10;

  return (
    <Card>
      <CardHeader className="pb-3">
        <CardTitle className="text-lg flex items-center gap-2">
          <ImageIcon className="w-5 h-5" />
          {t("dishImages.title")}
        </CardTitle>
        <CardDescription>
          {t("dishImages.description", { current: displayImages.length, max: 10 })}
          {displayImages.length > 1 && (
            <span className="block mt-1 text-xs">
              {t("dishImages.dragToReorder")}
            </span>
          )}
        </CardDescription>
      </CardHeader>
      <CardContent>
        {isLoading ? (
          <div className="flex items-center justify-center py-8">
            <Loader2 className="w-6 h-6 animate-spin" />
          </div>
        ) : (
          <div className="space-y-4">
            {/* Image Grid with Drag and Drop */}
            {displayImages.length > 0 && (
              <DndContext
                sensors={sensors}
                collisionDetection={closestCenter}
                onDragEnd={handleDragEnd}
              >
                <SortableContext
                  items={displayImages.map((img) => img.id)}
                  strategy={rectSortingStrategy}
                >
                  <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-3">
                    {displayImages.map((image, index) => (
                      <SortableImage
                        key={image.id}
                        image={image}
                        index={index}
                        onDelete={(id, url) => setDeleteConfirm({ id, url })}
                      />
                    ))}
                  </div>
                </SortableContext>
              </DndContext>
            )}

            {/* Upload Button */}
            {canAddMore && (
              <div>
                <input
                  ref={fileInputRef}
                  type="file"
                  accept="image/*"
                  className="hidden"
                  onChange={handleFileSelect}
                  disabled={addImage.isPending}
                />
                <Button
                  type="button"
                  variant="outline"
                  className="w-full"
                  onClick={() => fileInputRef.current?.click()}
                  disabled={addImage.isPending}
                >
                  {addImage.isPending ? (
                    <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                  ) : (
                    <Upload className="w-4 h-4 mr-2" />
                  )}
                  {t("dishImages.upload")}
                </Button>
              </div>
            )}

            {!canAddMore && (
              <p className="text-sm text-muted-foreground text-center">
                {t("dishImages.maxReached")}
              </p>
            )}
          </div>
        )}

        {/* Delete Confirmation - Rendered in portal to avoid form interference */}
        <AlertDialog open={!!deleteConfirm} onOpenChange={handleDialogClose}>
          <AlertDialogContent 
            onClick={(e) => e.stopPropagation()}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                e.stopPropagation();
              }
            }}
          >
            <form onSubmit={(e) => e.preventDefault()}>
              <AlertDialogHeader>
                <AlertDialogTitle>{t("dishImages.deleteConfirmTitle")}</AlertDialogTitle>
                <AlertDialogDescription>
                  {t("dishImages.deleteConfirmDescription")}
                </AlertDialogDescription>
              </AlertDialogHeader>
              <AlertDialogFooter className="mt-4">
                <Button
                  type="button"
                  variant="outline"
                  onClick={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    if (!isDeleting) {
                      setDeleteConfirm(null);
                    }
                  }}
                  disabled={isDeleting}
                >
                  {t("common.cancel")}
                </Button>
                <Button
                  type="button"
                  variant="destructive"
                  onClick={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    handleDeleteConfirm();
                  }}
                  disabled={isDeleting}
                >
                  {isDeleting ? <Loader2 className="w-4 h-4 mr-2 animate-spin" /> : null}
                  {t("common.delete")}
                </Button>
              </AlertDialogFooter>
            </form>
          </AlertDialogContent>
        </AlertDialog>
      </CardContent>
    </Card>
  );
}
